.hide {
  border: 1px solid rgba(0, 0, 0, 0);
  position: relative;
  box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0);
}

.hide:after {
  content: "•••";
  background-color: #fff;
  border-radius: 22px;
  align-items: center;
  justify-items: center;
  display: grid;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.hide + .hide {
  display: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

